import React, { Component, PropTypes } from 'react';
import { Button, Toast, WhiteSpace, Flex, DatePicker, List, Picker } from 'antd-mobile';
import moment from 'moment';
const today = moment().format('YYYY-MM-DD');
const tomorrow = moment().add(1, 'd').format('YYYY-MM-DD');
const theDayAfterTomorrow = moment().add(2, 'd').format('YYYY-MM-DD');

const queryIcon = require('../../images/query@3x.png');
class Query extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
          pageNo: 1,
          pageSize: 10,
          date: today, //
          isCustomDate: false, //
          startTime: '', //
          duration: '', //
          area: '',
          building: '', //
          floor: '', //
          minNum: '', //
          maxNum: '', //
          hasProjector: '', //
          isCollapse: true, //
        }
        this.collapse = this.collapse.bind(this);
        this.createQuery = this.createQuery.bind(this);
        this.search = this.search.bind(this);
        this.onDurationChange = this.onDurationChange.bind(this);
        this.onFloorChange = this.onFloorChange.bind(this);
    }

    //
    dateChoose(value, custom) {
        switch(value) {
        case 0: //
          this.setState({
            date: custom.format('YYYY-MM-DD'),
            isCustomDate: true
          })
        break;
        case 1: //
          this.setState({
            date: today,
            isCustomDate: false
          })
        break;
        case 2: //
          this.setState({
            date: tomorrow,
            isCustomDate: false
          })
        break;
        case 3: //
          this.setState({
            date: theDayAfterTomorrow,
            isCustomDate: false
          })
        break;
        }
    }
    //
    onDurationChange(e){
        const duration = e.target.value.trim() - 0;
        //durationNaN
        //00
        if(isNaN(duration) || duration < 0) {
            // 
        } else if(duration == 0){
            this.setState({duration: ''})
        } else {
            this.setState({duration})
        }
    }
    //
    onFloorChange(e) {
        const floor = e.target.value.trim() - 0;
        //durationNaN
        //00
        if(isNaN(floor) || floor < 0 || floor > 7) {
            // 
        } else if(floor == 0){
            this.setState({floor: ''})
        } else {
            this.setState({floor})
        }
    }
    //
    search() {
        const { onSearch } = this.props;
        const { date, startTime, end, minNum, maxNum, hasProjector
                ,duration, area, building, floor, pageSize, pageNo } = this.state;
        let buildingValue = '';
        let areaValue = '';
        if(building) {
            buildingValue = building.split('-').pop();
            areaValue = building.split('-')[0];

        }
        const params = {
            date, startTime ,end, minNum, maxNum, hasProjector
            ,duration, area: areaValue, building:buildingValue, floor, pageSize, pageNo
        }
        if( onSearch && typeof onSearch == 'function') {
            Toast.loading('...', 0, () => {});

            onSearch(params);
        }
        //
        this.collapse(true)
    }
    ///
    collapse(isCollapse) {
        this.setState({isCollapse})
    }
    // 
    formatArray = (arr = []) => {
        return arr.map(item => {
            return {label: `${item.area}-${item.building}`, value: `${item.area}-${item.building}`}
        })
    }
    createQuery() {
        const { date, isCustomDate, startTime, duration, building, floor
            ,minNum, maxNum, hasProjector, isCollapse } = this.state;
        const {areas} = this.props;
        if(!isCollapse) {
            //DatePickerList.Item
            //DatePick
            //extrawarning
            //issuehttps://github.com/ant-design/ant-design-mobile/issues/616
            const CustomDatePickerChildren = (props) => (
                <div onClick={props.onClick}>
                    {props.children}
                </div>
            );
            const startTimeData = [
              {value: '09:00', label: '09:00'},
              {value: '10:00', label: '10:00'},
              {value: '11:00', label: '11:00'},
              {value: '12:00', label: '12:00'},
              {value: '13:00', label: '13:00'},
              {value: '14:00', label: '14:00'},
              {value: '15:00', label: '15:00'},
              {value: '16:00', label: '16:00'},
              {value: '17:00', label: '17:00'},
              {value: '18:00', label: '18:00'},
              {value: '19:00', label: '19:00'},
              {value: '20:00', label: '20:00'},
              {value: '21:00', label: '21:00'},
            ];
            return (
                <div className="bsbb mb20">
                    <div className="query-box">
                        <WhiteSpace size="md" />
                        <span className='f30'></span>
                        <WhiteSpace size="xs" />
                        <Flex wrap="wrap">
                            <div className={"sunl-radio" + ((date == today) && !isCustomDate ? ' sunl-radio-checked' : '')} onClick={() => {this.dateChoose(1)}}></div>
                            <div className={"sunl-radio" + ((date == tomorrow) && !isCustomDate ? ' sunl-radio-checked' : '')} onClick={() => {this.dateChoose(2)}}></div>
                            <div className={"sunl-radio" + ((date == theDayAfterTomorrow) && !isCustomDate ? ' sunl-radio-checked' : '')} onClick={() => {this.dateChoose(3)}}></div>
                            <DatePicker mode="date" onChange={(v) => { this.dateChoose(0, v) }} minDate={moment()} maxDate={moment().add(6, 'd')}>
                                <CustomDatePickerChildren>
                                    <div className={"sunl-radio" + (isCustomDate ? ' sunl-radio-checked sunl-radio-customdate' : '')}>{ isCustomDate ? date : ''}</div>
                                </CustomDatePickerChildren>
                            </DatePicker>
                        </Flex>
                        <WhiteSpace size="xs" />
                    </div>
                    <List.Item wrap>
                        <div className='fl query-item-label'>
                          <span className='f30 query-span'></span>
                        </div>
                        <div className='fl' >

                            <Picker cols={1} data={startTimeData}
                              onChange={(v) => this.setState({ startTime: v[0] })}
                            >
                              <CustomDatePickerChildren><input value={startTime} className='query-item-input' readOnly={true}/></CustomDatePickerChildren>
                            </Picker>
                        </div>
                    </List.Item>
                    <List.Item wrap>
                        <div className='fl query-item-label'>
                            <span className='f30 query-span'></span>
                        </div>
                        <div className='fl'>
                            <Picker cols={1} data={[{value: 1, label: 1},{value: 2, label: 2},{value: 3, label: 3},{value: 4, label: 4},{value: 5, label: 5},{value: 6, label: 6},{value: 7, label: 7}]}
                              onChange={(v) => this.setState({ duration: v[0] })}
                            >
                              <CustomDatePickerChildren><input value={duration} className='query-item-input' readOnly={true}/><span className="pl10 f30"></span></CustomDatePickerChildren>

                            </Picker>


                        </div>
                    </List.Item>
                    <List.Item wrap>
                        <WhiteSpace size="xs" />
                        <span className='f30' className='f30'></span>
                        <WhiteSpace size="xs" />
                        <WhiteSpace size="xs" />
                        <Flex wrap="wrap">
                            <div className={"sunl-radio" + (building == '' ? ' sunl-radio-checked' : '')} onClick={() => {this.setState({building: ''})}}></div>
                            {/*<div className={"sunl-radio-180" + (building == '4' ? ' sunl-radio-checked' : '')} onClick={() => {this.setState({building: '4'})}}>4</div>
                                                        <div className={"sunl-radio-180" + (building == '6' ? ' sunl-radio-checked' : '')} onClick={() => {this.setState({building: '6'})}}>6</div>
                                                        <div className="sunl-radio" style={{'visibility': 'hidden'}}></div>
                                                        <div className={"mt10 sunl-radio-180" + (building == '-A' ? ' sunl-radio-checked' : '')} onClick={() => {this.setState({building: '-A'})}}>A</div>
                                                        <div className={"mt10 sunl-radio-180" + (building == '-B' ? ' sunl-radio-checked' : '')} onClick={() => {this.setState({building: '-B'})}}>B</div>*/}

                            <Picker cols={1} data={this.formatArray(areas)}
                              onChange={(v) => this.setState({ building: v[0] })}
                            >
                              <CustomDatePickerChildren>
                                <input placeholder="(-)" value={building} className='query-item-input' onChange={()=>{}}/>
                                <span className="pl10 f30"></span>
                              </CustomDatePickerChildren>

                            </Picker>                            
                        </Flex>
                        <WhiteSpace size="xs" />
                    </List.Item>
                    <List.Item wrap>
                        <WhiteSpace size="xs" />
                        <span className='f30'></span>
                        <WhiteSpace size="xs" />
                        <WhiteSpace size="xs" />
                        <Flex wrap="wrap">
                            <div className={"sunl-radio" + (floor == '' ? ' sunl-radio-checked' : '')} onClick={() => {this.setState({floor: ''})}}></div>

                            <Picker cols={1} data={[{value: 1, label: 1},{value: 2, label: 2},{value: 3, label: 3},{value: 4, label: 4},{value: 5, label: 5},{value: 6, label: 6},{value: 7, label: 7}]}
                              onChange={(v) => this.setState({ floor: v[0] })}
                            >
                              <CustomDatePickerChildren>
                                <input placeholder="1-7" value={floor} className='query-item-input' onChange={this.onFloorChange} />
                                <span className="pl10 f30"></span>
                              </CustomDatePickerChildren>

                            </Picker>
                        </Flex>
                        <WhiteSpace size="xs" />
                    </List.Item>
                    <List.Item wrap>
                        <WhiteSpace size="xs" />
                        <span className='f30'></span>
                        <WhiteSpace size="xs" />
                        <WhiteSpace size="xs" />
                        <Flex wrap="wrap">
                            <div className={"sunl-radio" + ((minNum == '' && maxNum == '') ? ' sunl-radio-checked' : '')} onClick={() => {this.setState({minNum: '', maxNum: ''})}}></div>
                            <div className={"sunl-radio" + ((minNum == '' && maxNum == 8) ? ' sunl-radio-checked' : '')} onClick={() => {this.setState({minNum: '', maxNum: 8})}}>8</div>
                            <div className={"sunl-radio" + ((minNum == 8 && maxNum == 15) ? ' sunl-radio-checked' : '')} onClick={() => {this.setState({minNum: 8, maxNum: 15})}}>8-15</div>
                            <div className={"sunl-radio sunl-radio-customdate" + ((minNum == 15 && maxNum == '') ? ' sunl-radio-checked' : '')} onClick={() => {this.setState({minNum: 15, maxNum: ''})}}>15</div>
                        </Flex>
                        <WhiteSpace size="xs" />
                    </List.Item>
                    <List.Item wrap>
                        <WhiteSpace size="xs" />
                        <span className='f30'></span>
                        <WhiteSpace size="xs" />
                        <WhiteSpace size="xs" />
                        <Flex wrap="wrap">
                            <div className={"sunl-radio" + (hasProjector === '' ? ' sunl-radio-checked' : '')} onClick={() => {this.setState({hasProjector: ''})}}></div>
                            <div className={"sunl-radio" + (hasProjector === 1 ? ' sunl-radio-checked' : '')} onClick={() => {this.setState({hasProjector: 1})}}></div>
                            <div className={"sunl-radio" + (hasProjector === 0 ? ' sunl-radio-checked' : '')} onClick={() => {this.setState({hasProjector: 0})}}></div>
                        </Flex>
                        <WhiteSpace size="md" />
                    </List.Item>
                    <div className="query-box" style={{paddingBottom:'20px'}}>

                        <div onClick={this.search} className="query-button">
                          <img src={queryIcon} className="mr20" style={{width: '0.5333rem', height: 'auto', verticalAlign: '-10%'}}/>
                        </div>
                        <WhiteSpace size="md" />

                    </div>

                </div>
            )
        } else {
            return (
                <div className="bsbb psr" style={{paddingTop:'0.92rem', marginBottom: '0.26667rem'}}>
                    <Flex wrap>
                        <div className="query-banner" onClick={() => {this.collapse(false)}}></div>
                    </Flex>
                    <div className="query-box f30" style={{color: "#888"}} onClick={() => {this.collapse(false)}}>
                        <WhiteSpace size="md" />
                        <Flex wrap="wrap">
                            <Flex.Item>{date}</Flex.Item>
                            <Flex.Item className="tar">{
                                (() => {
                                    // if(startTime != 0 && duration != '') {
                                    //     return moment(startTime).format('HH:MM') + ' - ' + moment(startTime).add(duration, 'h').format('HH:MM')
                                    // } else {
                                    //     return '';
                                    // }
                                    if(startTime != '') {
                                        return startTime
                                    } else {
                                        return '';
                                    }
                                })()

                            }</Flex.Item>
                        </Flex>
                        <WhiteSpace size="xs" />
                        <Flex wrap="wrap">
                            <Flex.Item>{building == '' ? '' : (building + '')}</Flex.Item>
                            <Flex.Item className="tar">{
                                (() => {
                                    if(minNum == '' && maxNum == '') {
                                        return '';
                                    } else if(minNum !='' && maxNum == '') {
                                        return '15';
                                    } else if(minNum == '' && maxNum != '') {
                                        return '8';
                                    } else if(minNum != '' && maxNum != '') {
                                        return '8-15';
                                    }
                                })()

                            }</Flex.Item>
                        </Flex>
                        <WhiteSpace size="xs" />
                        <Flex wrap="wrap">
                            <Flex.Item>{hasProjector == '' ? '' : (hasProjector ? '' : '')}</Flex.Item>
                            <Flex.Item className="tar">{floor == '' ? '' : (floor + '')}</Flex.Item>
                        </Flex>
                        <WhiteSpace size="md" />
                    </div>

                </div>
            )
        }
    }
    render() {
        return this.createQuery();
    }
}

export default Query;



