
import global from './global';
 import moment from 'moment';

var util = {

  formatDate(date){

  },

 deepCopy(data){
     return JSON.parse(JSON.stringify(data));
 },

 dispatchAsync(action){
     setTimeout(()=>{
        global.store.dispatch(action);
      }, 0);
 },

 isEmptyObject(e) {
    var t;
    for (t in e)
        return !1;
    return !0
 },
 /**
  * URL
  * @return {Object} {key:value}
  */
  getURLParams() {
   function parseParams(str) {
     var rs = {};
     var i = str.indexOf('?')
     if (i >= 0) {
       str = str.substr(i + 1);
       var params = str.split('&');
       params.forEach(function (s) {
         var p = s.split('=');
         if (p.length >= 2) {
           rs[p[0]] = p[1];
         }
       });
     }
     return rs;
   }

   return Object.assign({}, parseParams(location.search), parseParams(location.hash));
 },

 addContextUrl(url){
   var contextPath = location.pathname.replace('/index.html', '');
   return contextPath + url;
 },



  /**
   * eg:2016-6-18 20:00
   * @param {Date}
   * @return {String}
   */
  formatDateTime(time){
    return moment(time).format('YYYY-MM-DD H:mm');
  },
    /**
   * eg:6.18
   * @param {Date}
   * @return {String}
   */
  formatDate(time){
    return moment(time).format('M.D');
  },
    /**
   * 24eg:20:30
   * @param {Date}
   * @return {String}
   */
  formatTime(time){
    return moment(time).format('H:mm');
  },

/**
 * 
 * @str {String|Number}
 * @return {Date}
 */
  parseDate(str){
    if(!str){
      return new Date();
    }
    if(typeof str == 'number'){
      return new Date(str);
    }
    //
    if(typeof str == 'string' && str.indexOf('+08') < 0){
      str = str + '+08:00';
    }
    return new Date(str);
  },



  /**
   * 
   * @str {String|Number} 
   * @start {Number} 0
   * @end {Number} 0
   * @wildchar {String} 
   * @count {Number} startend
   * @return {newStr} 
   */
  mosaicStr(str = '', start = 0, end, wildchar = '*', count) {
    const reg = new RegExp('^(.{' + start + '})(.*)(.{' + (typeof end == 'undefined' ? 0 : (str.length - end - 1)) + '})$', 'g');
    let newStr = str.replace(reg, (match, p1, p2, p3) => {
      return p1 + new Array(p2.length + 1).join(wildchar) + p3
    });
    return newStr
  }

}

window.mosaicStr = util.mosaicStr

export default util;



